
.filial {
  padding-top: 13rem;
  margin-bottom: 11rem;
  @include md {
    margin-bottom: 6rem;
    padding-top: 5rem; }
  .container {
    position: relative; }
  &__title {
    color: #160605;
    font-weight: 500;
    font-size: 3.6rem;
    line-height: 4.3rem;
    margin-bottom: 4rem;
    @include md {
      font-size: 2.4rem;
      line-height: 2.9rem; } }
  &__checbox {
    margin-bottom: 3rem;
    @include md {
      display: flex; } }
  &__sity {
    font-size: 2.4rem;
    line-height: 2.9rem;
    border-radius: 2rem;
    padding: 1.5rem 2.5rem;
    transition: all 0.3s ease;
    &.is-active {
      color: #fff;
      background-color: #81BC06; }
    @include md {
      font-size: 1.8rem;
      line-height: 2.2rem; } }
  &__wrapper {
    display: none;
    &.is-active {
      display: flex; }
    @include lg {
      flex-direction: column-reverse; }
    @include md {
      flex-direction: column; } }
  &__cone {
    width: 220px;
    height: 220px;
    max-width: none;
    @include md {
      width: 39rem;
      height: 39rem; }

    &-wrapper {
      position: absolute;
      top: -12rem;
      right: 6rem;
      @include lg {
        top: 25rem;
        left: -5rem; }
      @include md {
        top: 7.5rem;
        z-index: -1;
        left: auto;
        right: -9rem; }
      &::after {
        content: '';
        display: block;
        width: 130px;
        height: 30px;
        background: rgba(11, 11, 11, 0.6);
        filter: blur(27.5px);
        border-radius: 999px;
        transform: rotate(18.89deg); } } }
  &__map {
    width: 595px;
    flex-shrink: 0;
    margin-right: 3rem;
    @include xl {
      width: 500px; }
    @include lg {
      width: 720px;
      height: 210px;
      display: flex;
      align-items: center;
      justify-content: center; }
    @include md {
      width: 100%;
      margin-bottom: 3rem;
      padding: 0 1rem; }
    img {
      @include xl {
        height: 100%; }
      @include lg {
        width: 100%; }
      @include md {
        border-radius: 2rem; } } }
  .adress {
    width: 100%;
    display: flex;
    flex-direction: column;
    @include lg {
      width: auto;
      margin-left: 12.5rem;
      margin-bottom: 4rem; }
    @include md {
      margin-bottom: 0;
      margin-left: 0;
      flex-direction: row;
      overflow: auto; }
    &__item {
      display: flex;
      flex-direction: column;
      width: 50%;
      @include md {
        width: 232px;
        & > *:not(:first-child) {
          margin-right: 2rem; } }
      & > *:not(:first-child):not(:last-child) {
        margin-bottom: 1rem; }
      &-wrapper {
        display: flex;
        @include lg {
          justify-content: space-between; }
        &:not(:last-child) {
          @include md {
            border-bottom: none; }
          padding-bottom: 2rem;
          border-bottom: 1px solid #767676;
          margin-bottom: 2rem;
          @include lg;
          @include md {
            flex-shrink: 0; } } } }
    &__title {
      color: #160605;
      font-size: 1.8rem;
      line-height: 2.2rem;
      margin-bottom: 2rem; }
    &__info {
      color: #160605;
      font-size: 1.4rem;
      line-height: 1.7rem; }
    &__line {} } }
