.faq {
  margin-bottom: 110px;
  @include md {
    margin-bottom: 50px; }
  &__wrapper {
    & > *:not(:last-child) {
      margin-bottom: 2rem; } }
  &__title {
    font-weight: 500;
    font-size: 36px;
    line-height: 43px;
    margin-bottom: 60px; }
  &__item {
    &.is-active {
      .faqitem__footer,
      .faqitem__button-close {
        display: flex; }
      .faqitem__button-open {
        display: none; } } } }

.faqitem {
  background-color: #fff;
  position: relative;
  border-radius: 20px;
  &__img {
    margin-right: 30px;
    @include md {
      margin-right: 10px; } }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    padding: 20px 40px;
    @include md {
      display: block; } }
  &__title {
    font-size: 24px;
    line-height: 29px;
    @include md {
      margin-bottom: 20px; } }
  &__button {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 2.9rem;
    position: relative;
    z-index: 0;
    &-close {
      display: none; }
    &::after {
      content: '';
      position: absolute;
      width: 3rem;
      height: 3rem;
      border-radius: 99%;
      background-color: #C2C2C2;
      right: -1.7rem;
      z-index: -1;
      top: 50%;
      transform: translateY(-50%); } }
  &__footer {
    display: none;
    flex-direction: column;
    border-top: 1px solid #767676;
    padding: 20px 40px;
    & > *:not(:last-child) {
      margin-bottom: 10px; } }
  &__text {
    color: #160605;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none; } }
