.header {
	font-size: 1.6rem;
	padding: 1.6rem 0;
	&__logowrapper {
		text-decoration: none;
		display: flex;
		@include xsm {
			align-items: center; } }
	&__nav {
		display: flex;
		align-items: center;
		margin-left: auto;
		justify-content: flex-end;
		@include lg {
			display: none; } }
	&__item {
		display: flex;
		width: 100%;
		@include lg {
			align-items: center; }
		&-title {
			line-height: 1.9rem;
			text-align: center;
			@include md {
				font-size: 14px;
				line-height: 17px;
				display: flex;
				flex-direction: column;
				align-items: center;
				&:not(:first-child) {
					padding-left: .8rem; } }
			a {
				text-decoration: none;
				color: #160605;
				font-size: 1.6rem;
				line-height: 1.9rem;
				white-space: nowrap; }
			&:not(:first-child) {
				padding-left: 6rem;
				@include xl {
					padding-left: 4rem; } } } }
	&__logo {
		flex-shrink: 0;
		max-width: 7.4rem;
		@include xsm {
			max-width: 5.4rem; } }
	&__title-wrapper {
		display: flex;
		flex-direction: column;
		margin-left: 3rem;
		max-width: 20rem;
		@include lg {
			margin-left: 1.5rem; }
		@include xsm {
			margin-left: 1rem; }
		> *:not(:last-child) {
			margin-bottom: 1rem; } }
	&__wrapper {
		display: flex;
		align-items: center;
		@include xsm {
			flex-direction: column;
			align-items: flex-start; } }
	&__title {
		color: #160605;
		font-size: 2.5rem;
		line-height: 30px;
		@include xsm {
			font-size: 1.4rem;
			line-height: 1.4rem; } }
	&__subtitle {
		color: #160605;
		font-size: 1.4rem;
		line-height: 1.7rem;
		white-space: nowrap;
		@include xsm {
			font-size: 1.2rem;
			line-height: 1.4rem; } } }

.menu {
	width: 100%;
	background-color: white;
	padding: 30px 24px;
	position: fixed;
	opacity: 0;
	transform: translateX(-100%);
	pointer-events: none;
	min-height: 100vh;
	transition: all 0.3s ease;
	z-index: 2;
	top: 0;
	left: 0;
	&__wrapper {
		text-align: end;
		padding-top: 9rem;
		& > *:not(:last-child) {
			margin-bottom: 3rem; } }
	&__phone {
		text-decoration: none;
		color: #160605;
		font-weight: 700;
		font-size: 16px;
		line-height: 19px;
		display: block; }
	&__mail {
		text-decoration: none;
		color: #160605;
		font-size: 18px;
		line-height: 22px;
		display: block; }
	&__item {
		display: flex;
		justify-content: end; }
	&__text {
		color: #160605;
		margin-left: 15px;
		text-decoration: none; }
	&__lot {
		display: block;
		text-decoration: none;
		font-weight: 500;
		font-size: 1.6rem;
		line-height: 1.9rem;
		color: #160605; }
	&.is-opened {
		opacity: 1;
		pointer-events: all;
		transform: translateX(0); } }

.hamburger {
	display: none;
	z-index: 3;
	@include lg {
		margin-left: auto;
		display: flex; }
	.hamburger-box {
		@include md {
			height: 28px; }
		@include xsm {
			height: 16px; } }
	.hamburger-box,
	.hamburger-inner,
	.hamburger-inner::after,
	.hamburger-inner::before {
		@include md {
			width: 4rem; }
		@include xsm {
			width: 2.4rem; } }
	.hamburger-inner::after {
		@include md {
			top: 28px; }
		@include xsm {
			top: 8px; } }
	.hamburger-inner::before {
		@include md {
			top: 14px; }
		@include xsm {
			top: 16px; } }
	&.is-active {
		position: absolute;
		.hamburger-inner::after {
			top: 32px; } } }
